:root {
  /* set amount of light & saturation to change for darker & lighter color variants */
  --shader: 5%;

  /* calculate for lighter & darker color variations */
  --lighter1: calc(1 * var(--shader));
  --lighter2: calc(2 * var(--shader));
  --lighter3: calc(3 * var(--shader));
  --lighter4: calc(4 * var(--shader));
  --darker1: calc(1 * var(--shader));
  --darker2: calc(2 * var(--shader));
  --darker3: calc(3 * var(--shader));
  --darker4: calc(4 * var(--shader));

  // System color

  --c-system: hsl(0 0% 8%);

  // Primary color
  --c-primary-hue: 199;
  --c-primary-sat: 64%;
  --c-primary-light: 46%;
  --c-primary: hsl(var(--c-primary-hue), var(--c-primary-sat), var(--c-primary-light));

  // Primary color light gradients
  --c-primary-light-1: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) - var(--lighter1)), calc(var(--c-primary-light) + var(--lighter1)));
  --c-primary-light-2: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) - var(--lighter2)), calc(var(--c-primary-light) + var(--lighter2)));
  --c-primary-light-3: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) - var(--lighter3)), calc(var(--c-primary-light) + var(--lighter3)));
  --c-primary-light-4: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) - var(--lighter4)), calc(var(--c-primary-light) + var(--lighter4)));

  // Primary color dark gradients
  --c-primary-dark-1: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) + var(--darker1)), calc(var(--c-primary-light) - var(--lighter1)));
  --c-primary-dark-2: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) + var(--darker2)), calc(var(--c-primary-light) - var(--lighter2)));
  --c-primary-dark-3: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) + var(--darker3)), calc(var(--c-primary-light) - var(--lighter3)));
  --c-primary-dark-4: hsl(var(--c-primary-hue), calc(var(--c-primary-sat) + var(--darker4)), calc(var(--c-primary-light) - var(--lighter4)));

  // Secondary/Accent color
  --c-secondary-hue: 31;
  --c-secondary-sat: 78%;
  --c-secondary-light: 47%;
  --c-secondary: hsl(var(--c-secondary-hue), var(--c-secondary-sat), var(--c-secondary-light));

  // Secondary/Accent color light gradients
  --c-secondary-light-1: hsl(
    var(--c-secondary-hue),
    calc(var(--c-secondary-sat) - var(--lighter1)),
    calc(var(--c-secondary-light) + var(--lighter1))
  );
  --c-secondary-light-2: hsl(
    var(--c-secondary-hue),
    calc(var(--c-secondary-sat) - var(--lighter2)),
    calc(var(--c-secondary-light) + var(--lighter2))
  );
  --c-secondary-light-3: hsl(
    var(--c-secondary-hue),
    calc(var(--c-secondary-sat) - var(--lighter3)),
    calc(var(--c-secondary-light) + var(--lighter3))
  );
  --c-secondary-light-4: hsl(
    var(--c-secondary-hue),
    calc(var(--c-secondary-sat) - var(--lighter4)),
    calc(var(--c-secondary-light) + var(--lighter4))
  );

  // Secondary/Accent color dark gradients
  --c-secondary-dark-1: hsl(var(--c-secondary-hue), calc(var(--c-secondary-sat) + var(--darker1)), calc(var(--c-secondary-light) - var(--lighter1)));
  --c-secondary-dark-2: hsl(var(--c-secondary-hue), calc(var(--c-secondary-sat) + var(--darker2)), calc(var(--c-secondary-light) - var(--lighter2)));
  --c-secondary-dark-3: hsl(var(--c-secondary-hue), calc(var(--c-secondary-sat) + var(--darker3)), calc(var(--c-secondary-light) - var(--lighter3)));
  --c-secondary-dark-4: hsl(var(--c-secondary-hue), calc(var(--c-secondary-sat) + var(--darker4)), calc(var(--c-secondary-light) - var(--lighter4)));

  // Gray color
  --c-gray-hue: 0;
  --c-gray-sat: 0%;
  --c-gray-light: 50%;
  --c-gray: hsl(var(--c-gray-hue), var(--c-gray-sat), var(--c-gray-light));

  // Gray color light gradients
  --c-gray-light-1: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) + calc(var(--lighter1) * 1.5)));
  --c-gray-light-2: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) + calc(var(--lighter2) * 1.5)));
  --c-gray-light-3: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) + calc(var(--lighter3) * 1.5)));
  --c-gray-light-4: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) + calc(var(--lighter4) * 1.5)));

  // Gray color dark gradients
  --c-gray-dark-1: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) - calc(var(--lighter1) * 1.5)));
  --c-gray-dark-2: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) - calc(var(--lighter2) * 1.5)));
  --c-gray-dark-3: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) - calc(var(--lighter3) * 1.5)));
  --c-gray-dark-4: hsl(var(--c-gray-hue), var(--c-gray-sat), calc(var(--c-gray-light) - calc(var(--lighter4) * 1.5)));

  // Light-Gray color
  --c-light-gray-hue: 0;
  --c-light-gray-sat: 0%;
  --c-light-gray-light: 90%;
  --c-light-gray: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), var(--c-light-gray-light));

  // Light-Gray color light gradients
  --c-light-gray-light-1: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) + 2.5%));
  --c-light-gray-light-2: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) + 5%));
  --c-light-gray-light-3: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) + 7.5%));
  --c-light-gray-light-4: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) + 10%));

  // Light-Gray color dark gradients
  --c-light-gray-dark-1: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) - 2.5%));
  --c-light-gray-dark-2: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) - 5%));
  --c-light-gray-dark-3: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) - 7.5%));
  --c-light-gray-dark-4: hsl(var(--c-light-gray-hue), var(--c-light-gray-sat), calc(var(--c-light-gray-light) - 10%));

  // Success color
  --c-success-hue: 145;
  --c-success-sat: 100%;
  --c-success-light: 32%;
  --c-success: hsl(var(--c-success-hue), var(--c-success-sat), var(--c-success-light));

  // Success color light gradients
  --c-success-light-1: hsl(var(--c-success-hue), calc(var(--c-success-sat) - var(--lighter1)), calc(var(--c-success-light) + var(--lighter1)));
  --c-success-light-2: hsl(var(--c-success-hue), calc(var(--c-success-sat) - var(--lighter2)), calc(var(--c-success-light) + var(--lighter2)));
  --c-success-light-3: hsl(var(--c-success-hue), calc(var(--c-success-sat) - var(--lighter3)), calc(var(--c-success-light) + var(--lighter3)));
  --c-success-light-4: hsl(var(--c-success-hue), calc(var(--c-success-sat) - var(--lighter4)), calc(var(--c-success-light) + var(--lighter4)));

  // Success color dark gradients
  --c-success-dark-1: hsl(var(--c-success-hue), calc(var(--c-success-sat) + var(--darker1)), calc(var(--c-success-light) - var(--lighter1)));
  --c-success-dark-2: hsl(var(--c-success-hue), calc(var(--c-success-sat) + var(--darker2)), calc(var(--c-success-light) - var(--lighter2)));
  --c-success-dark-3: hsl(var(--c-success-hue), calc(var(--c-success-sat) + var(--darker3)), calc(var(--c-success-light) - var(--lighter3)));
  --c-success-dark-4: hsl(var(--c-success-hue), calc(var(--c-success-sat) + var(--darker4)), calc(var(--c-success-light) - var(--lighter4)));

  // Alert color
  --c-alert-hue: 38;
  --c-alert-sat: 100%;
  --c-alert-light: 52%;
  --c-alert: hsl(var(--c-alert-hue), var(--c-alert-sat), var(--c-alert-light));

  // Alert color light gradients

  --c-alert-light-1: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) - var(--lighter1)), calc(var(--c-alert-light) + var(--lighter1)));
  --c-alert-light-2: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) - var(--lighter2)), calc(var(--c-alert-light) + var(--lighter2)));
  --c-alert-light-3: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) - var(--lighter3)), calc(var(--c-alert-light) + var(--lighter3)));
  --c-alert-light-4: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) - var(--lighter4)), calc(var(--c-alert-light) + var(--lighter4)));

  // Alert color dark gradients

  --c-alert-dark-1: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) + var(--darker1)), calc(var(--c-alert-light) - var(--lighter1)));
  --c-alert-dark-2: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) + var(--darker2)), calc(var(--c-alert-light) - var(--lighter2)));
  --c-alert-dark-3: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) + var(--darker3)), calc(var(--c-alert-light) - var(--lighter3)));
  --c-alert-dark-4: hsl(var(--c-alert-hue), calc(var(--c-alert-sat) + var(--darker4)), calc(var(--c-alert-light) - var(--lighter4)));

  // Info color
  --c-info-hue: 214;
  --c-info-sat: 61%;
  --c-info-light: 42%;
  --c-info: hsl(var(--c-info-hue), var(--c-info-sat), var(--c-info-light));

  // Info color light gradients
  --c-info-light-1: hsl(var(--c-info-hue), calc(var(--c-info-sat) - var(--lighter1)), calc(var(--c-info-light) + var(--lighter1)));
  --c-info-light-2: hsl(var(--c-info-hue), calc(var(--c-info-sat) - var(--lighter2)), calc(var(--c-info-light) + var(--lighter2)));
  --c-info-light-3: hsl(var(--c-info-hue), calc(var(--c-info-sat) - var(--lighter3)), calc(var(--c-info-light) + var(--lighter3)));
  --c-info-light-4: hsl(var(--c-info-hue), calc(var(--c-info-sat) - var(--lighter4)), calc(var(--c-info-light) + var(--lighter4)));

  // Info color dark gradients
  --c-info-dark-1: hsl(var(--c-info-hue), calc(var(--c-info-sat) + var(--darker1)), calc(var(--c-info-light) - var(--lighter1)));
  --c-info-dark-2: hsl(var(--c-info-hue), calc(var(--c-info-sat) + var(--darker2)), calc(var(--c-info-light) - var(--lighter2)));
  --c-info-dark-3: hsl(var(--c-info-hue), calc(var(--c-info-sat) + var(--darker3)), calc(var(--c-info-light) - var(--lighter3)));
  --c-info-dark-4: hsl(var(--c-info-hue), calc(var(--c-info-sat) + var(--darker4)), calc(var(--c-info-light) - var(--lighter4)));

  // Error color
  --c-error-hue: 359;
  --c-error-sat: 80%;
  --c-error-light: 58%;
  --c-error: hsl(var(--c-error-hue), var(--c-error-sat), var(--c-error-light));

  // Error color light gradients
  --c-error-light-1: hsl(var(--c-error-hue), calc(var(--c-error-sat) - var(--lighter1)), calc(var(--c-error-light) + var(--lighter1)));
  --c-error-light-2: hsl(var(--c-error-hue), calc(var(--c-error-sat) - var(--lighter2)), calc(var(--c-error-light) + var(--lighter2)));
  --c-error-light-3: hsl(var(--c-error-hue), calc(var(--c-error-sat) - var(--lighter3)), calc(var(--c-error-light) + var(--lighter3)));
  --c-error-light-4: hsl(var(--c-error-hue), calc(var(--c-error-sat) - var(--lighter4)), calc(var(--c-error-light) + var(--lighter4)));

  // Error color dark gradients
  --c-error-dark-1: hsl(var(--c-error-hue), calc(var(--c-error-sat) + var(--darker1)), calc(var(--c-error-light) - var(--lighter1)));
  --c-error-dark-2: hsl(var(--c-error-hue), calc(var(--c-error-sat) + var(--darker2)), calc(var(--c-error-light) - var(--lighter2)));
  --c-error-dark-3: hsl(var(--c-error-hue), calc(var(--c-error-sat) + var(--darker3)), calc(var(--c-error-light) - var(--lighter3)));
  --c-error-dark-4: hsl(var(--c-error-hue), calc(var(--c-error-sat) + var(--darker4)), calc(var(--c-error-light) - var(--lighter4)));

  ///////////////////////////////////////////////////////////////////////////////
  // Specific Colors
  ///////////////////////////////////////////////////////////////////////////////

  // Absolute colors / common colors to avoid linting alerts on hardcoded values
  --c-system: #141414; // System color
  --c-white: #fafafa; // White
  --c-dark-white: #f7f7f7; // White
  --c-black: #1a1a1a; // Black

  // Text colors
  --c-text-base: var(--c-system);
  --c-text-secondary: var(--c-secondary);
  --c-text-link: var(--c-text-secondary);

  // Social brand colors
  --c-facebook: #3b5998;
  --c-twitter: #55acee;
  --c-google: #d34836;
  --c-youtube: #bb0000;
  --c-whatsapp: #7ed321;
  --c-instagram: #8a3ab9;

  // Highlight color
  --c-highlight: #fffed4;
}
